<template>
    <v-container >
         
        <h1 class="headline my-4">
            <v-icon size="30">{{ collection.icon }}</v-icon>
             {{ collectionTitle }} 
        </h1>
        
        <v-toolbar :class="{ 'mb-6': !filters}"   :dense="$vuetify.breakpoint.xsOnly" flat v-if="hasButtons" >
            <template  v-for="(button, index) in collectionButtons"  >
                <v-spacer :key="index" v-if="button.spacer"></v-spacer> 
                <v-btn 
                    v-else
                    :key="index" 
                    :small="$vuetify.breakpoint.xsOnly"
                    class="mx-1"
                
                    v-bind="typeof button.bind == 'function' ? button.bind(vm) : button.bind"
                    v-on="typeof button.on == 'function' ? button.on(vm) : button.on"
                    outlined>
                    <v-icon v-if="button.icon">{{button.icon}}</v-icon>
                    <span v-if="!$vuetify.breakpoint.xsOnly"> {{button.title}} </span>
                </v-btn>
                </template>
        </v-toolbar>
        <div style=" position: relative" v-if="filters">

            <v-row :class="['white', 'ma-0', 'mb-6', {'border-top': hasButtons}, 'pb-3', 'transition']" 
            ref="filterWraper"
            :style="filterHeight"
            >
                <v-col cols="12" class="py-0"><small class="font-weight-bold"> <v-icon size="20">mdi-filter</v-icon> Filters</small></v-col>
                <v-col cols="12" v-for="(field, index) in filters" :key="index" v-bind="field.col">
                
                    <component  class="mx-1"
                                :is="inputs[field.input]" 
                                v-bind="getProps(typeof field.bind == 'function' ? field.bind(vm) : field.bind,`filter_${collectionName}_${field.name}`)"
                                v-on="typeof field.on == 'function' ? field.on(vm) : field.on"
                                outlined rounded dense hide-details
                                :value="setFilterValue($route.query[field.name] || null, field)"
                                @input="filter(field,$event)"
                                clearable
                                server-value
                            />
                </v-col> 

            </v-row>
             <div class="mask" v-if="$vuetify.breakpoint.xsOnly && !filterExpanded && filters.length > 2"> </div>   
             <v-btn v-if="$vuetify.breakpoint.xsOnly && filters.length > 2" elevation="2" @click="filterExpanded = !filterExpanded" small fab absolute bottom right color="primary">
                 <v-icon> {{ filterExpanded ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
             </v-btn>
        </div>
        <app-table class="mb-6" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly" v-if="collection && collection.items.total" :items="totals" :headers="collection.items.total.headers"></app-table>



        <app-table
            ref="table"
            v-model="selected"
            :server-items-length="serverItemsLength"
            :page="Number($route.query.page) || 1"
            :items-per-page="Number($route.query.limit) || 10"
            :sort-by="sortBy"
            :sort-desc="sortDesc"
            :loading="loading"  
            :headers="headers"
            :items="items"
            :footer-props="footerProps"
            item-key="_id"
            multi-sort
            :show-select="hasButtons"
            @update:options="updateTable"
            
        >
          <template v-slot:top="{ pagination, options }" >
                <v-row class="ma-0   border-top " >
                    <v-col cols="12" md="6" class="pa-0" v-if="selected.length">
                        <v-alert class="ma-0">
                            <span class="font-weight-bold">({{ selected.length }}) items has been selected</span>
                            <v-btn text small color="info" class="mx-1" outlined @click="selected = []"> Unselect All</v-btn> 
                        </v-alert>
                    </v-col>
                    <v-col cols="12" :md="selected.length ? 6 : 12" class="pa-0">
                        <v-data-footer style="border-top:0" @update:options="updateTable" :items-per-page-options="footerProps['items-per-page-options']" :pagination="pagination" :options="options" >
                        </v-data-footer>
                    </v-col>
                </v-row>
               
            </template>
        </app-table>




    <v-dialog v-model="dialog.value"
        v-bind="typeof dialog.bind == 'function' ? dialog.bind(vm) : dialog.bind"
        v-on="typeof dialog.on == 'function' ? dialog.on(vm) : dialog.on"
    >
        <component 
            v-if="dialog.component"
            :is="dialog.component.is || dialog.component" 
            v-bind="typeof dialog.component == 'function' ? {} : typeof dialog.component.bind == 'function' ? dialog.component.bind(vm) : (dialog.component.bind || {})"
            v-on="typeof dialog.component.on == 'function' ? dialog.component.on(vm) : dialog.component.on"
            ></component>
    </v-dialog>
    </v-container>
</template>

<script>
import collections from './../../collections'
import AppTable from './../../components/DataTable'

 import   {
  VTextField,
  VSelect,
  VSwitch,
} from 'vuetify/lib'
 
import AppAutoComplete from './../../components/Autocomplete'
import AppDatePicker from './../../components/DatePicker'
import AppDateRange from '../../components/DateRange'


import $ from 'jquery'
import debounce from 'debounce'

import utility from '../../utility'
// import { mapActions } from 'vuex'
export default {
    name: 'Items',

    data () {
        return {
            dialog: {
                value: false
            },
            loading: false,
            serverItemsLength: 0,
            items: [],
            selected: [],
            totals:[],
            props: {}, // filter props
            filterExpanded: false
        }
    },
    components:{
        AppTable
    },
    computed: {
        filterHeight () {
            const h = this.$refs.filterWraper ? this.$refs.filterWraper.scrollHeight+'px' : 'auto'
            
           
            return this.filterExpanded ? {
                 height: this.$vuetify.breakpoint.xsOnly ? h : 'auto',
                  overflow: 'hidden',
            } : {
                height: this.$vuetify.breakpoint.xsOnly ? '180px' : 'auto',
                overflow: 'hidden',
            }
        },
        inputs: () => ({
            text: VTextField,
            select: VSelect,
            switch: VSwitch,
            date: AppDatePicker,
            range: AppDateRange,
            autocomplete: AppAutoComplete,
        }),
        vm () {
            return this
        },
        collectionName () {
            return this.$route.params.collection
        },
        collection () {
            return collections.get(this.collectionName)
        },
        collectionTitle () {
            return this.collection ? (this.collection.title.plural || this.collection.title) : ''
        },
        collectionItems () {
            return this.collection.items || {}
        },
        collectionButtons () {
            if (!this.collectionItems.buttons) return false
            
            return this.collectionItems.buttons.filter(btn => {
                if (btn.if) {
                    switch (typeof btn.if) {
                        case 'function':
                        return btn.if(this);
                        case 'boolean':
                        return btn.if;
                        default:
                        return true;
                    }
                } else
                return true
            })
        },
        hasButtons () {
            return Boolean(this.collectionButtons && this.collectionButtons.length)
        },
        filters () {
            if (this.collectionItems.filters)
                return this.collectionItems.filters.filter(input => {
                    switch (typeof input.if) {
                        case 'function':
                        return input.if(this);
                        case 'boolean':
                        return input.if;
                        default:
                        return true;
                    }
                
                })
            return false
        },
        headers () {

            if (!this.collection) return []

            const { fields } = this.permissions
            let headers = []
            if( fields ) {
                const filteredFields = []
                Object.keys(fields).forEach(f => {
                if (fields[f])
                    filteredFields.push(atob(f))
                })
                headers =  this.collectionItems.headers.filter(e => filteredFields.indexOf(e.value) !== -1 || (e.value == 'actions' && (this.permissions.write.value || this.permissions.update.value || this.permissions.delete.value) ) )
            } else
                headers =  this.collectionItems.headers

            return headers.filter(e => !e.hide)
        },
        footerProps () {
            let limits = [5, 10, 20, 50]
            const qLimit = Number(this.$route.query.limit)
            if(!isNaN(qLimit) && limits.indexOf(qLimit) == -1)
                limits.push(qLimit)
            return {'items-per-page-options':  limits}
        },
        sortBy () {
            const { query } = this.$route
            if (query['sortBy[]'])
                return Array.isArray(query['sortBy[]']) ? query['sortBy[]'] : [query['sortBy[]']]
            else
                return undefined
        },
        sortDesc () {
            const { query } = this.$route
            if (query['sortDesc[]'])
                return Array.isArray(query['sortDesc[]']) ? query['sortDesc[]'].map(e => eval(e)) : [eval(query['sortDesc[]'])]
            else
                return undefined
        }
    },
    watch:{
        'dialog.value' (value) {
            if (!value) {
                this.dialog = { value }
            }
        
        },
        collectionName () {
            this.dialog = { value: false }
            this.props = {}
            this.selected = []
            this.items = []
            this.totals = []
        },
        user: {
            handler: 'updateComponent',
        },
        '$route': {
            handler: 'updateComponent',
            immediate: true
        }
    },
    methods: {
        updateTable (options) {
            const { page, itemsPerPage, sortBy, sortDesc} = options
            const query = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error

            if (query['sortBy[]'])
                delete query['sortBy[]']

            if (query['sortDesc[]'])
                delete query['sortDesc[]']

            
            const pushRoute = query.page < page

            query.page = page
           
            query.limit = itemsPerPage

            if (sortBy.length)
                query.sortBy = sortBy

            if (sortDesc.length)    
                query.sortDesc = sortDesc

            const uri =  this.$route.path + '?' + $.param(query)

            if ($.param(query) != $.param(this.$route.query))
                if (pushRoute)
                    this.$router.push(uri)
                else
                    this.$router.replace(uri)
        
        },
        updateComponent () {

            if (!this.collection)
                this.$router.replace('/404')
            else
                this.fetch()
        },
        async fetch () {
            this.loading= true
 
            // fetch table data
            const result = await this.get({ collection: this.collectionName + window.location.search  }) 
            const { totalDocs, docs } = result.data
            this.items = docs
            this.serverItemsLength = totalDocs
            // console.log(docs);
            //fetch totals
            if (this.collection.items.total) {
            const totalResult = await this.get({ collection: this.collectionName + '/total' + window.location.search  }) 
                this.totals = totalResult.data
                // console.log(this.totals);
            }
            
            this.loading= false
        },
        setFilterValue (value, field) {
            
            if (field.search && this.$route.query['orFields[]']) {
                const orFields = this.$route.query['orFields[]']
                const orValues = this.$route.query['orValues[]']
                const index = orFields.indexOf(field.name)
                value = orValues[index]
            }

            const type = utility.filters.getType(field)
            const fieldValue = utility.filters.setValue(value, type)
            return fieldValue
        },
        filter: debounce(function (field, value) {

            const query = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
             
            const type = utility.filters.getType(field)
            const fieldValue = utility.filters.getValue(value, type, field.searchOptions)
            

             if (query['orFields[]']) {
                 query.orFields = Object.assign([],query['orFields[]'])
                 delete query['orFields[]']
             }

            if (query['orValues[]']) {
                query.orValues = Object.assign([],query['orValues[]'])
                delete query['orValues[]']
            }


            
            // remove field from query with empty value
            if ((!value || !value.length) && query[field.name]){
                delete query[field.name]
            }
            else if (field.search && (!value || !value.length) && query.orFields) {
                const find = query.orFields.indexOf(field.name)
                query.orFields.splice(find, 1)
                query.orValues.splice(find, 1)

                if (Array.isArray(field.search)) {
                    field.search.forEach((name, index) => {
                        query.orFields.splice(index, 1)
                        query.orValues.splice(index, 1)
                    })
                } else {
                    const sind = query.orFields.indexOf(field.search)
                    query.orFields.splice(sind, 1)
                    query.orValues.splice(sind, 1)
                }
              
            }
            else if (field.search && value) { // search multi fields `{ $or: [ 'field1', 'field2' ]}`
            
                query.orFields = [field.name]
                query.orValues = [fieldValue]
                if (Array.isArray(field.search))
                    field.search.forEach(f => {
                        query.orFields.push(f)
                        query.orValues.push(fieldValue)
                    })
                else {
                    query.orFields.push(field.search)
                    query.orValues.push(fieldValue)
                }
               
            
            
            } else if (value)
                query[field.name] = fieldValue


           
   
            
            // console.log(query, $.param(query), $.param(this.$route.query));


            // translate to query string and route
            query.page = 1
            const uri =  this.$route.path + '?' + $.param(query)
            if ($.param(query) != $.param(this.$route.query))
                this.$router.replace(uri) 
        
        }, 500),
        getProps (props, refName) {
            if (this.props[refName]) {
                return Object.assign({}, props, this.props[refName])
            } else {
                return props
        }
    },
    },
    created () {
        // console.log(this);
    }

}
</script>
<style scoped>
.border-top {
        border-top: thin solid rgba(0, 0, 0, 0.12) !important;
}
.mask {
    background: linear-gradient(to bottom, rgba(255,0,0,0), rgb(0 0 0 / 8%));
    position: absolute;
    width: 100%;
    bottom: 0px;
    height: 35px;
}
.transition {
    transition: all .2s ease-in-out;
}
</style>